.item, .context-menu .search {
  color: #fff;
  padding: 4px;
  border-bottom: 1px solid rgba(69, 103, 255, 0.8);
  background-color: rgba(110, 136, 255, 0.8);
  cursor: pointer;
  width: 100%;
  position: relative; }
  .item:first-child, .context-menu .search:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; }
  .item:last-child, .context-menu .search:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px; }
  .item:hover, .context-menu .search:hover {
    background-color: rgba(130, 153, 255, 0.8); }

.context-menu {
  left: 0;
  top: 0;
  position: fixed;
  padding: 10px;
  width: auto;
  margin-top: -20px;
  margin-left: -auto/2; }
  .context-menu.docked {
    background-color: #6e88ff;
    margin: 0;
    bottom: 0;
    top: auto;
    padding: 0;
    width: 100%; }
    .context-menu.docked .search, .context-menu.docked > div {
      display: inline-block;
      width: auto;
      height: 100%;
      border: 0;
      border-radius: 0; }

.context-menu .subitems {
  width: 100% !important; }
